blockquote {
  padding-right: 1rem;
  border-right: 2px var(--tblr-border-style) var(--tblr-border-color);
}
blockquote p {
  margin-bottom: 1rem;
}
blockquote cite {
  display: block;
  text-align: left;
}
blockquote cite:before {
  content: " ";
}

ul, ol {
  padding-right: 1.5rem;
}

hr, .hr {
  margin: 2rem 0;
}

dl dd:last-child {
  margin-bottom: 0;
}

pre {
  padding: 1rem;
  background: var(--tblr-bg-surface-dark);
  color: var(--tblr-light);
  border-radius: var(--tblr-border-radius);
}
pre code {
  background: transparent;
}

code {
  background: var(--tblr-code-bg);
  padding: 2px 4px;
  border-radius: var(--tblr-border-radius);
}

kbd,
.kbd {
  border: var(--tblr-border-width) var(--tblr-border-style) var(--tblr-border-color);
  display: inline-block;
  box-sizing: border-box;
  max-width: 100%;
  font-size: var(--tblr-font-size-h5);
  font-weight: var(--tblr-font-weight-medium);
  line-height: 1;
  vertical-align: baseline;
  border-radius: var(--tblr-border-radius);
}

img {
  max-width: 100%;
}

.list-unstyled {
  margin-right: 0;
}

/**
Selection
 */
::-moz-selection {
  background-color: rgba(var(--tblr-primary-rgb), 0.16);
}
::selection {
  background-color: rgba(var(--tblr-primary-rgb), 0.16);
}

/**
Links
 */
[class^=link-].disabled, [class*=" link-"].disabled {
  color: var(--tblr-disabled-color) !important;
  pointer-events: none;
}

/**
Subheader
 */
.subheader {
  font-size: 0.625rem;
  font-weight: var(--tblr-font-weight-bold);
  text-transform: uppercase;
  letter-spacing: 0.04em;
  line-height: 1rem;
  color: var(--tblr-secondary);
}

.chart {
  display: block;
  min-height: 10rem;
}
.chart text {
  font-family: inherit;
}

.chart-sm {
  height: 2.5rem;
}

.chart-lg {
  height: 15rem;
}

.chart-square {
  height: 5.75rem;
}

/**
Chart sparkline
 */
.chart-sparkline {
  position: relative;
  width: 4rem;
  height: 2.5rem;
  line-height: 1;
  min-height: 0 !important;
}

.chart-sparkline-sm {
  height: 1.5rem;
}

.chart-sparkline-square {
  width: 2.5rem;
}

.chart-sparkline-wide {
  width: 6rem;
}

.chart-sparkline-label {
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 0.625rem;
}
.chart-sparkline-label .icon {
  width: 1rem;
  height: 1rem;
  font-size: 1rem;
}

.offcanvas-header {
  border-bottom: var(--tblr-border-width) var(--tblr-border-style) rgba(4, 32, 69, 0.1);
}

.offcanvas-footer {
  padding: 1.5rem 1.5rem;
}

.offcanvas-title {
